
.period {
    background: white;
}
.ignored {
    font-style: italic;
    text-decoration: line-through;
    color: #777;
}
.month-heading{
    background: #5c9ccc;
    color: white;
    text-shadow: -1px 1px 0 rgba(0,0,0,.3);
    border-top: 1em rgba(255,255,255,0) solid;
}
.month-heading.deja-envoyee {
    background: #970000;
}
.month-heading .message-erreur {
    font-weight: 400;
    display: block;
}
.match { border-left: #5c9ccc 4px solid;
}
.correspondance-choose {
    border-top: solid #efede4 thin;
    font-size: 1em;
    cursor: pointer;
    padding: 4px 1em;
}
.correspondance-choose.selected {
    background: #5c9ccc;
    color: white;
}
.correspondance-choose:hover {
    background: #5c9ccc;
    color: white;
}
.correspondance-choose h3 {
    margin: 0;
    padding: 0;
    font-size: 12px;
    font-weight: 600;
}
.jour {
    white-space: nowrap;
    font-weight: 200;
    color: black;
    text-align: right;
}
.jour-description {
    font-size: .9em;
}
.jour-heures {
    text-align: right;
}
.list {

    margin: 1em;
    border: solid thin #d9deda;
}



